/* home page slider */
.homeBg {
  transition: -webkit-transform ease 0.3s;
  transition: transform ease 0.3s;
  transition: transform ease 0.3s, -webkit-transform ease 0.3s;
}

.homeBg:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

.slider {
  background: transparent;
  height: 80px;

  border-radius: 40px;
  padding: 0 5px;
}

.field {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
}

.field input {
  -webkit-appearance: none;
  height: 13px;
  width: 100%;
  border-radius: 7px;
  outline: none;
  border: none;
}

.field input::-webkit-slider-thumb {
  -webkit-appearance: none;
  border: none;
  height: 30px;
  width: 30px;
  border: 3px solid white;
  background: #FFFFFF;
  border-radius: 50%;
}

.field input::-moz-range-thumb {
  -moz-appearance: none;
  border: none;
  height: 30px;
  width: 30px;
  border: 3px solid white;
  background: #FFFFFF;
  border-radius: 50%;
}

.second span.show {
  -webkit-transform: translateX(-50%) scale(1);
          transform: translateX(-50%) scale(1);
}

.second span::after {
  background: #FFFFFF;

}

